@import "font-smoothing";

:root,
body {
    margin: 0;
    padding: 0;
    overflow: hidden;
    font-family: 'Redaction';
    background-color: #000;
    color: #fff;
}


#container {
    width: 700px;
    height: 275px;
    background-color: transparent;
    overflow: hidden;
}

#timer {
    color: #fff;
    line-height: 1em;
    font-size: 200px;
    font-weight: bold;
    display: flex;
    justify-content: space-between;
    align-items: center;
    text-align: center;
    height: 100%;
}

#timer .hide {
    opacity: 0;
}

.letter {
    overflow: hidden;
    height: 195px;
    width: 165px;
}

.letter,
.divider {
    opacity: 1;
    transition-property: opacity;
    transition-duration: 100ms;
    transition-timing-function: linear;
}

#divider {
    width: 40px;
    height: 120px;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-between;
}

.circle {
    width: 40px;
    height: 40px;
    border-radius: 20px;
    background-color: currentColor;
}
